body {
  font-family: Roboto;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
}

h1, h2, h3, h4, h5, h6, ul, p {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul > li {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

:root {
  --text-main-color: #fff;
  --text-clr-second: #ff001b;
  --text-films-main: #000;
  --text-films-second: #ff6b08;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding: 0 20px;
}

@media screen and (min-width: 320px) {
  .container {
    min-width: 320px;
    width: 100%;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding: 0 32px;
  }
}

.button {
  height: 44px;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  color: var(--text-main-color);
  background-color: var(--text-films-second);
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  padding: 13px 20px;
  font-family: inherit;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  display: flex;
}

